import Vue from 'vue'
import Vuex from 'vuex'


Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    products : [
      {
        itemId: 1,
        image: 'https://raw.githubusercontent.com/garnett0914/garnett0914.github.io/main/img/products/switch-mid.jpg',
        name: 'Nintendo Switch',
        price: 9780
      },
      {
        itemId: 2,
        image: 'https://raw.githubusercontent.com/garnett0914/garnett0914.github.io/main/img/products/ps5.jpg',
        name: 'PS5',
        price: 15980
      },
      {
        itemId: 3,
        image: 'https://raw.githubusercontent.com/garnett0914/garnett0914.github.io/main/img/products/xbox-series-x-mid.png',
        name: 'XBOX SERIES X',
        price: 15380
      },
      {
        itemId: 4,
        image: 'https://raw.githubusercontent.com/garnett0914/garnett0914.github.io/main/img/products/ps5-digital.jpg',
        name: 'PS5 Digital Edition',
        price: 12980
      },
      {
        itemId: 5,
        image: 'https://raw.githubusercontent.com/garnett0914/garnett0914.github.io/main/img/products/xbox-series-s.png',
        name: 'XBOX SERIES S',
        price: 9480
      },
      {
        itemId: 6,
        image: 'https://raw.githubusercontent.com/garnett0914/garnett0914.github.io/main/img/products/duelsense.jpg',
        name: 'DuelSense Remote Controller',
        price: 2180
      }
    ],
    priceFilter: 'Any',
    snackbar: {
      show: false,
      variant: 'success',
      message: 'Success! Item added to the cart.'
    },
    cart: [] 
  },
  getters: {
    priceFilter: state => {
      return state.priceFilter;
    },
    cartItems: state => {
      return state.cart;
    },
    cartTotalPrice: state => {
      let total = 0;
      state.cart.forEach(element => {
        total += element.item.price * element.quantity
      });
      return total;
    }
  },
  mutations: {
    addItemToCart(state, payload) {
      const { item, quantity } = payload
      const idx = state.cart.findIndex((product) => {
        return product.item.itemId === item.itemId
      })
      if (idx === -1) {
        state.cart.push({ item, quantity })
      } else {
        state.cart[idx].quantity += 1
      }
    },
    updateSnackbar(state, settings) {
      state.snackbar = {
        ...state.snackbar,
        ...settings
      }
    },
    updatepriceFilter(state, priceFilter) {
      state.priceFilter = priceFilter;
    },
    updateCart(state, itemId) {
      const idx = state.cart.findIndex((product) => {
        return product.item.itemId === itemId
      })

      if (idx !== -1)
        state.cart.splice(idx, 1);
    },
    updateCartQuantity(state, payload) {
      const { value, index } = payload;
      //console.log(value, index);
      state.cart[index].quantity = value;
    }
  },
  actions: {
    updatepriceFilter({commit}, payload) {
      commit('updatepriceFilter', payload);
    },
    updateCart({commit}, payload) {
      commit('updateCart', payload);
    },
    updateCartQuantity({commit}, payload) { 
      commit('updateCartQuantity', payload);
    }
  }
})
